/**@jsx jsx */
import { jsx } from 'theme-ui';
import React from 'react';
import { Link } from 'gatsby';
import styled from '@emotion/styled';

const StyledPostFooter = styled.div`
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
`;

const Wrapper = styled.div`
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
`;

const NavDiv = styled.div`
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0;
`;

const Footer = ({ previous, next }) => (
  <StyledPostFooter>
    {(previous || next) && (
      <Wrapper>
        {next && (
          <NavDiv className="post__nav-next">
            <h3>Next Post</h3>
            <Link
              to={'/post/' + next.post.slug}
              rel="next"
              sx={{
                color: 'primary',
              }}
            >
              ↩ {next.post.title}
            </Link>
          </NavDiv>
        )}
        {previous && (
          <NavDiv
            sx={{
              textAlign: 'right',
            }}
          >
            <h3>Previous post</h3>
            <Link
              to={'/post/' + previous.post.slug}
              rel="prev"
              sx={{
                color: 'primary',
              }}
            >
              {previous.post.title} ↪
            </Link>
          </NavDiv>
        )}
      </Wrapper>
    )}
  </StyledPostFooter>
);

export default Footer;
