/**@jsx jsx */
import { jsx } from 'theme-ui';
import React, { useEffect } from 'react';
import styled from '@emotion/styled';
import scrollama from 'scrollama';
import Curve from '../SVG/memphis-curve';
import { mediaQueries } from '../../design-system/blog-theme';

const StyledTOC = styled.div`
  position: sticky;
  height: max-content;
  grid-column: -3 / -1;
  max-height: 80vh;
  z-index: 1;
  max-width: 15em;
  padding: 1rem;
  top: 90px;
  right: 2rem;
`;

const ListTitle = styled.h5`
  text-transform: uppercase;
  font-size: 12px;
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 500;
  padding-left: 2px;
`;

const StyledList = styled.ul`
  position: relative;
  padding-left: 0.5rem;
`;

const StyledLink = styled.a`
  display: block;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-all;
`;

// generate TOC markup
const generateTOC = (data) => {
  if (!data.items) {
    return null;
  }
  return (
    <StyledList>{data.items.map((el, i) => generateList(el, i, (i + 1).toString()))}</StyledList>
  );
};

const generateList = (data, i, prefix) => {
  return (
    <li
      key={data.url}
      sx={{
        '&.active > svg': {
          display: 'inline-block',
          top: 0,
          left: 0,
          stroke: 'primary',
          fill: 'primary',
        },
      }}
    >
      <Curve
        sx={{
          display: 'none',
          width: '2rem',
          height: '1.5rem',
          position: 'absolute',
        }}
      />
      <StyledLink
        href={data.url}
        sx={{
          px: 4,
          py: 1,
          fontSize: 1,
          fontFamily: 'monospace',
          fontWeight: 500,
          color: 'black',
          '&:hover': {
            color: 'primary',
          },
        }}
      >
        <span>{prefix}.</span>
        <span>{data.title}</span>
      </StyledLink>
      {data.items && (
        <StyledList>
          {data.items.map((el, i) => {
            return generateList(el, i, prefix + '.' + (i + 1));
          })}
        </StyledList>
      )}
    </li>
  );
};

const TOC = ({ data }) => {
  useEffect(() => {
    // eslint-disable-next-line global-require
    require('intersection-observer');
    const scroller = scrollama();
    scroller
      .setup({
        step: '.markdown>h1,.markdown>h2,.markdown>h3,.markdown>h4',
        offset: 0,
      })
      .onStepEnter(({ element }) => {
        // reset
        const id = decodeURIComponent(element.id);
        [].forEach.call(document.querySelectorAll('.toc-wrapper ul li'), (el) => {
          el.classList.remove('active');
        });
        // activate current tag
        const current = document.querySelectorAll(`.toc-wrapper ul li a[href^="#${id}"]`)[0];
        if (current) {
          current.parentElement.classList.add('active');
        }
      });
    return () => {
      scroller.destroy();
    };
  }, []);

  return (
    <StyledTOC
      sx={{
        display: 'none',
        border: 'solid 2px',
        borderColor: 'black',
        boxShadow: 'memphis',
        [mediaQueries.md]: {
          display: 'block',
        },
      }}
    >
      <ListTitle
        sx={{
          color: 'offblack',
        }}
      >
        Table of contents
      </ListTitle>
      <section className="toc-wrapper">{generateTOC(data)}</section>
    </StyledTOC>
  );
};

export default TOC;
