import React, { Fragment } from 'react';
import styled from '@emotion/styled';
import { DiscussionEmbed } from 'disqus-react';
import Layout from '../../layout/Page';
import PostFooter from '../../components/PostFooter';
import PostContent from '../../components/PostContent';
import TOC from '../../components/TOC';
import ScrollToTop from '../../components/ScrollToTop';
import SEO from '../../components/SEO/SEO';

import PageProgressBar from '../../components/PageProgressBar';

const StyledCenter = styled.main`
  grid-column: 3/ -3;
`;

import 'katex/dist/katex.min.css';

const Post = ({ data, previous, next }) => {
  const { post } = data;
  const tags = post.fields.tags;
  const disqusConfig = {
    shortname: process.env.GATSBY_DISQUS_NAME,
    config: { identifier: post.fields.title },
  };
  return (
    <Layout activeTag={'posts'}>
      <SEO
        title={post.fields.title}
        description={post.excerpt}
        lang="en"
        keywords={tags}
        isBlogPost={true}
      />
      {post.tableOfContents.items && post.tableOfContents.items.length > 0 && (
        <TOC data={post.tableOfContents} />
      )}
      <ScrollToTop />
      <StyledCenter>
        <PostContent data={data} />
        <PostFooter previous={previous} next={next} />
        <div className="post__comment">
          <DiscussionEmbed {...disqusConfig} />
        </div>
      </StyledCenter>
      <PageProgressBar />
    </Layout>
  );
};

export default Post;
