import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';
import usePageScroll from '../../hooks/usePageScroll';

const StyledProgress = styled.div`
  position: fixed;
  bottom: 0;
  z-index: 99;

  background: ${({ bgColor }) => (bgColor ? bgColor : '#2ab3c4')};
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  transition: width 200ms ease-in-out;
`;

const PageProgress = ({ bgColor, height, ...props }) => {
  const [width, setWidth] = useState(null);

  const { lastScroll } = usePageScroll();

  useEffect(() => {
    const { scrollHeight, clientHeight, scrollTop } = document.documentElement
      ? document.documentElement
      : {
          scrollHeight: 0,
          clientHeight: 0,
          scrollTop: 0,
        };
    const winScroll = scrollTop;
    const winHeight = scrollHeight - clientHeight;
    const scrolled = `${(winScroll / winHeight) * 100}%`;
    if (winHeight > 0) {
      return setWidth(scrolled);
    } else {
      return setWidth(0);
    }
  }, [lastScroll]);

  return (
    <StyledProgress
      bgColor={bgColor}
      style={{
        height: height ? height : '4px',
        width: width,
      }}
    />
  );
};

export default PageProgress;
