/**@jsx jsx */
import { jsx } from 'theme-ui';
import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';
import { ArrowUp } from 'react-feather';
import { mediaQueries } from '../../design-system/blog-theme';
import { easeInOutQuad } from '../animations/utils/easings';
import usePageScroll from '../../hooks/usePageScroll';

const Wrapper = styled.div`
  position: fixed;
  bottom: 5rem;
  left: 5rem;
  z-index: 9999;
  border-radius: 100%;
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.5);
  transition: left ease-in 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  &.hidden {
    left: -5rem;
  }
`;

const ScrollToTop = () => {
  const { lastScroll } = usePageScroll();
  const [shown, setShown] = useState(false);
  useEffect(() => {
    if (lastScroll > 0) {
      setShown(true);
    } else {
      setShown(false);
    }
  }, [lastScroll]);

  return (
    <Wrapper
      className={shown ? '' : 'hidden'}
      sx={{
        width: '3rem',
        height: '3rem',
        [mediaQueries.sm]: {
          display: 'none',
        },
        [mediaQueries.lg]: {
          display: 'flex',
        },
        backgroundColor: 'primary',
        '&:hover': {
          cursor: 'pointer',
        },
      }}
      onClick={(e) => {
        e.preventDefault();
        // start position
        let py = Math.floor(window.pageYOffset);
        let percent = 0;
        let id = setInterval(() => {
          let delta = (percent * 10) / 500;
          let nextp = easeInOutQuad(delta);
          percent += 1;
          delta < 1
            ? window.scrollTo(0, (1 - nextp) * py)
            : (clearInterval(id), window.scrollTo(0, 0));
        }, 15);
      }}
    >
      <ArrowUp
        sx={{
          width: '1.5rem',
          height: '1.5rem',
          color: 'white',
          borderRadius: '100%',
          zIndex: '1',
        }}
      ></ArrowUp>
    </Wrapper>
  );
};

export default ScrollToTop;
