/**@jsx jsx */
import { jsx } from 'theme-ui';
import React from 'react';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import styled from '@emotion/styled';
import Tag from '../PostTag/PostTag';
import { Link } from 'gatsby';
import { Tag as TagIcon, Folder, Clock, Calendar } from 'react-feather';

const StyledPostHeader = styled.header`
  padding-bottom: 1rem;
  padding-top: 1rem;
  padding-left: 2rem;
  .post__description {
    padding-bottom: 1rem;
    padding-top: 1rem;
    display: flex;
    > small {
      font-size: 90%;
      padding-right: 1rem;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
    }
    .post__categoryLink {
      font-weight: 600;
    }
  }
`;

const StyledPostContent = styled.div`
  position: relative;
  padding: 2rem;
  margin-bottom: 2rem;
  scroll-behavior: smooth;
  counter-reset: chapter section subsection subsubsection subsubsubsection;
  > ul,
  ol {
    padding-left: 28px;
  }
  h1,
  h2,
  h3,
  h4,
  h5 {
    margin-top: 1rem;
    margin-bottom: 1rem;
    &::before {
      margin-right: 0.5em;
      color: #888;
    }
  }
`;

const StyledPostFooter = styled.div`
  padding-bottom: 3rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  > .post__tags-title {
    display: flex;
    align-items: center;
    font-size: 15px;
    font-weight: lighter;
  }
  > ul.post__tags-list {
    padding-left: 0.2rem;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    > li {
      padding-left: 0.2rem;
    }
  }
`;

const PostContent = ({ data }) => {
  const { post } = data;
  const tags = post.fields.tags;
  return (
    <div>
      <StyledPostHeader>
        <h1
          sx={{
            fontSize: 10,
            fontWeight: 'heading',
          }}
        >
          {post.fields.title}
        </h1>
        <div className="post__description">
          <small className="post__date">
            <Calendar sx={{ mr: 1 }} /> {post.fields.date}
          </small>
          <small className="post__category">
            <Folder sx={{ mr: 1, color: 'primary' }} />
            <Link
              className="post__categoryLink"
              to={`/categories/` + post.fields.category}
              sx={{
                color: 'text',
                '&:hover': {
                  color: 'primary',
                },
                px: 1,
                py: 1,
              }}
            >
              {post.fields.category}
            </Link>
          </small>
          <small>
            <Clock sx={{ mr: 1 }} /> {post.timeToRead} min to read
          </small>
        </div>
      </StyledPostHeader>
      <StyledPostContent
        className="markdown"
        sx={{
          backgroundColor: 'offwhite',
          color: 'black',
          boxShadow: 'darkhighlight',
        }}
      >
        <MDXRenderer>{post.body}</MDXRenderer>
      </StyledPostContent>
      <StyledPostFooter>
        <div className="post__tags-title">
          <TagIcon />
        </div>
        <ul className="post__tags-list">
          {tags.length > 0 &&
            tags.map((tag, index) => {
              return (
                <li key={index} className="post__tags-li">
                  <Tag tag={tag} />
                </li>
              );
            })}
        </ul>
      </StyledPostFooter>
    </div>
  );
};

const PostContentWrapper = ({ data }) => <PostContent data={data} />;

export default PostContentWrapper;
